import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { TypeOfMediumComponent } from './type-of-medium.component';

const routes: Routes = [
  {
    path: '',
    component: TypeOfMediumComponent
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class TypeOfMediumRoutingModule { }
